import React, {Component, PureComponent} from "react";
import PropTypes from "prop-types";

let floatingLabelId = 0;

class OraFloatingLabelWrapper extends PureComponent {
    static propTypes = {
        className: PropTypes.string,
        autoComplete: PropTypes.bool,
        maximumWidth: PropTypes.bool,
        children: PropTypes.any,
        overrideClass: PropTypes.string
    };

    static defaultProps = {
        autoComplete: false,
        maximumWidth: false
    };

    state = {
        floatingLabelId: floatingLabelId++
    };

    componentDidMount() {
   //     this.loadModule();
    }

    loadModule() {
        OPL.UI.loadModules(this.refs.wrapperRef, [
            {path: "core/modules/floating-label"}
        ]);
    }

    render() {
        const {className, overrideClass, autoComplete, maximumWidth, children} = this.props;
        let classList = overrideClass || "o-floating-label opl-floating-label-line opl-floating-label-line--white";
            if (className) {
                classList += " " + className;
            }
            if (autoComplete) {
                classList += " opl-autocomplete";
            }
            if (maximumWidth) {
                classList += " u-w-100";
            }
        return (
            <div ref={"wrapperRef"}
                id={"floating-label-wrapper-" + this.state.floatingLabelId}
                className={classList}
            >
                {children}
            </div>
        );
    }
}


export default OraFloatingLabelWrapper;